import MaterialIcon from '@material/react-material-icon';
import React, { useEffect, useState } from 'react';
import { Link, useParams } from 'react-router-dom';
import {
  Button,
  Card,
  CardBody,
  CardTitle,
  Col,
  FormGroup,
  Input,
  Label,
  Row,
  Form,
  Spinner,
} from 'reactstrap';
import { useQuery } from '@tanstack/react-query';
import useAxios from '../../hooks/useAxios';
import { alert } from '../../components/toast/Toast';
import getBase64 from '../../utils/generateFile';

const fields = [
  {
    id: 0,
    title: 'Surat Penyampaian Penawaran',
    name: 'dok_surat_penyampaian_penawaran',
    template: true,
    doc_file: '',
  },
  {
    id: 1,
    title: 'Formulir Isian Kualifikasi',
    name: 'dok_formulir_isian_kualifikasi',
    template: true,
    doc_file: 'https://api.ptpema.co.id/vendor_file/templates/data_kualifikasi.docx',
  },
  {
    id: 2,
    title: 'Fakta Integritas',
    name: 'dok_fakta_integritas',
    template: true,
    doc_file: 'https://api.ptpema.co.id/vendor_file/templates/fakta_integritas.docx',
  },
  {
    id: 3,
    title: 'HSE Plan',
    name: 'dok_hse_plan',
  },
  {
    id: 4,
    title: 'Jaminan Penawaran',
    name: 'dok_jaminan_penawaran',
  },
  {
    id: 5,
    title: 'Kelengkapan Izin Usaha',
    name: 'dok_kelengkapan_izin_usaha',
  },
  {
    id: 6,
    title: 'List Peralatan',
    name: 'dok_list_peralatan',
  },
  {
    id: 7,
    title: 'List Man Power + CV',
    name: 'dok_listmanpower_plus_cv',
  },
  {
    id: 8,
    title: 'Metode Pelaksanaan (Jasa)',
    name: 'dok_metode_pelaksanaan',
  },
  {
    id: 9,
    title: 'Surat Penawaran Komersial',
    name: 'dok_penawaran_komersial',
  },
  {
    id: 10,
    title: 'Perhitungan TKDN',
    name: 'dok_perhitungan_tkdn',
  },
  {
    id: 11,
    title: 'QA/QC Plan',
    name: 'dok_qaqc_plan',
  },
  {
    id: 12,
    title: 'Schedule Pekerjaan',
    name: 'dok_schedule_pekerjaan',
  },
  {
    id: 13,
    title: 'Struktur Organisasi Pekerjaan',
    name: 'dok_struktur_organisasi_pekerjaan',
  },
  {
    id: 14,
    title: '*Dokumen Wajib Lainnya',
    name: 'dok_wajib_lainnya',
  },
];

const UploadDocument = () => {
  const { slug } = useParams();
  const [tenderId, setTenderId] = useState();
  const [docsField, setDocsField] = useState({});
  const [keys, setKeys] = useState([]);
  const [selectedField, setSelectedField] = useState();
  const [selectedFieldTitle, setSelectedFieldTitle] = useState();
  const [file, setFile] = useState();
  const [uploading, setUploading] = useState(false);
  const [fieldIsOpen, setFieldIsOpen] = useState(false);
  const [isCompleted, setIsCompleted] = useState(false);
  const [isSubmitting, setIsSubmitting] = useState(false);

  const api = useAxios();

  const { data, refetch } = useQuery({
    queryKey: ['tender-detail'],
    queryFn: () =>
      api.get(`vapi/tender/peserta/${slug}`).then((res) => {
        return res.data.data;
      }),
  });

  useEffect(() => {
    setDocsField(data?.centang_dok_wajib && JSON.parse(data?.centang_dok_wajib));
    setTenderId(data?.id_tender);
  }, [data]);

  useEffect(() => {
    setKeys(docsField && Object.keys(docsField));
  }, [docsField]);

  useEffect(() => {
    const isCompleteArray = keys?.filter((k) => {
      return data[k] !== null;
    });
    const requestUploadArray = keys?.filter((k) => {
      return docsField[k] === true;
    });

    setIsCompleted(JSON.stringify(isCompleteArray) === JSON.stringify(requestUploadArray));
  }, [keys, docsField]);

  const handleChange = (e, fieldId, fieldTitle) => {
    setSelectedField(fieldId);
    setSelectedFieldTitle(fieldTitle);
    getBase64(e.target.files[0])
      .then((res) => setFile({ key: e.target.id, file: res.split(',')[1] }))
      .catch(() => setFile());
  };

  const handleUpload = async (e) => {
    e.preventDefault();
    setUploading(true);
    if (file !== undefined) {
      file.tender_id = tenderId;

      await api
        .post('vapi/tender/upload/dokumen', file)
        .then(() => {
          refetch();
          alert('success', `Dokumen ${selectedFieldTitle} berhasil diupload.`);
        })
        .catch(() => {
          alert('error', 'Something went wrong.');
        });
    } else {
      alert('error', 'Tidak ada file yang dipilih.');
    }
    setFile();
    setSelectedField();
    setUploading(false);
  };

  const handleSubmitDocument = async () => {
    setIsSubmitting(true);
    await api
      .get(`vapi/tender/submit-dokumen/${data?.id_peserta}`)
      .then(() => {
        refetch();
        alert('success', 'Dokumen tender berhasil dikirim.');
      })
      .catch(() => {
        alert('error', 'Something went wrong.');
      });
    setIsSubmitting(false);
  };

  return (
    <Card className="rounded-4">
      <CardBody>
        <Row>
          <Col md="8">
            <CardTitle tag="h4">{data?.nama_tender}</CardTitle>
          </Col>
          <Col md="4">
            {/* <CardTitle tag="h4" className="text-right float-right">
              <Badge className="text-success bg-light-success fw-bold float-right">Submitted</Badge>
            </CardTitle> */}
          </Col>
        </Row>
        <small>{data?.sistem_kualifikasi}</small>
        {fields?.map((f) => (
          <Row key={f.id}>
            <Form onSubmit={handleUpload}>
              {keys?.map(
                (k) =>
                  docsField[k] === true &&
                  f.name === k &&
                  (data[k] === null ? (
                    <FormGroup key={k}>
                      <Row className="mt-3">
                        <Col md="10">
                          <Label htmlFor={k}>{f.title}</Label>
                          <Input
                            type="file"
                            id={k}
                            name={k}
                            onChange={(e) => handleChange(e, f.id, f.title)}
                            accept="application/pdf"
                          />
                          <div className="d-flex justify-content-between">
                            <small className="text-muted">Upload file (max. 10mb)</small>
                            <i>
                              {f.template && (
                                <Link
                                  to={f.doc_file}
                                  className="text-decoration-none"
                                  target="_blank"
                                >
                                  <small>*Download template</small>
                                </Link>
                              )}
                            </i>
                          </div>
                        </Col>
                        <Col md="2 d-flex align-items-center pt-2">
                          {uploading && f.id === selectedField ? (
                            <Button type="button" color="dark" size="sm" disabled>
                              <div className="d-flex align-items-center gap-2">
                                <Spinner size="sm" />
                                Uploading..
                              </div>
                            </Button>
                          ) : (
                            <Button
                              type="submit"
                              size="sm"
                              color="dark"
                              required
                              disabled={f.id !== selectedField}
                            >
                              Upload
                            </Button>
                          )}
                        </Col>
                      </Row>
                    </FormGroup>
                  ) : selectedField === f.id && fieldIsOpen ? (
                    <FormGroup key={k}>
                      <Row className="mt-3">
                        <Col md="10">
                          <Label htmlFor={k}>Edit Dokumen {f.title}</Label>
                          <Input
                            type="file"
                            id={k}
                            name={k}
                            onChange={(e) => handleChange(e, f.id, f.title)}
                            accept="application/pdf"
                          />
                          <div className="d-flex justify-content-between">
                            <small className="text-muted">Upload file (max. 10mb)</small>
                            <i>
                              {f.template && (
                                <Link
                                  to={f.doc_file}
                                  className="text-decoration-none"
                                  target="_blank"
                                >
                                  <small>*Download template</small>
                                </Link>
                              )}
                            </i>
                          </div>
                        </Col>
                        <Col md="2 d-flex align-items-center pt-2">
                          {uploading && f.id === selectedField ? (
                            <Button type="button" color="dark" size="sm" disabled>
                              <div className="d-flex align-items-center gap-2">
                                <Spinner size="sm" />
                                Uploading..
                              </div>
                            </Button>
                          ) : (
                            <div className="d-flex gap-2">
                              <Button
                                type="submit"
                                size="sm"
                                color="secondary"
                                outline
                                onClick={() => {
                                  setFieldIsOpen(false);
                                  setSelectedField();
                                }}
                              >
                                Cancel
                              </Button>
                              <Button
                                type="submit"
                                size="sm"
                                color="dark"
                                required
                                disabled={f.id !== selectedField}
                              >
                                Upload
                              </Button>
                            </div>
                          )}
                        </Col>
                      </Row>
                    </FormGroup>
                  ) : (
                    <FormGroup key={k} className="bg-light rounded-4 px-3 pb-2 pt-1">
                      <Row className="mt-3">
                        <Col md="10">
                          <Label htmlFor={k}>{f.title}</Label>
                          <div className="d-flex gap-4">
                            <Link to="">{data[k].split('/')[3]}</Link>
                            {data?.status !== 'submit_dokumen' && (
                              <abbr title="Edit Dokumen">
                                <button
                                  className="text-muted"
                                  type="button"
                                  style={{ border: 'none', background: 'none' }}
                                  onClick={() => {
                                    setFieldIsOpen(true);
                                    setSelectedField(f.id);
                                  }}
                                >
                                  <MaterialIcon icon="edit" style={{ fontSize: '20px' }} />
                                </button>
                              </abbr>
                            )}
                          </div>
                        </Col>
                      </Row>
                    </FormGroup>
                  )),
              )}
            </Form>
            {/* <Col sm="12" lg="6">
                <FormGroup>
                  <Label htmlFor="file2">Formulir Isian Kualifikasi</Label>
                  <Input type="file" id="file2" name="file2" />
                  <div className="d-flex justify-content-between">
                    <small className="text-muted">Upload file (max. 10mb)</small>
                    <i>
                      <Link to="/" className="text-decoration-none">
                        <small>*Download template</small>
                      </Link>
                    </i>
                  </div>
                </FormGroup>
              </Col> */}
          </Row>
        ))}
        {/* <Row>
            <Col sm="12" lg="6">
              <FormGroup>
                <Label htmlFor="file3">Pakta Integritas</Label>
                <Input type="file" id="file3" name="file3" />
                <div className="d-flex justify-content-between">
                  <small className="text-muted">Upload file (max. 10mb)</small>
                  <i>
                    <Link to="/" className="text-decoration-none">
                      <small>*Download template</small>
                    </Link>
                  </i>
                </div>
              </FormGroup>
            </Col>
            <Col sm="12" lg="6">
              <FormGroup>
                <Label htmlFor="file4">HSE Plan</Label>
                <Input type="file" id="file4" name="file4" />
                <div className="d-flex justify-content-between">
                  <small className="text-muted">Upload file (max. 10mb)</small>
                </div>
              </FormGroup>
            </Col>
          </Row>
          <Row>
            <Col sm="12" lg="6">
              <FormGroup>
                <Label htmlFor="file5">List Man Power + CV</Label>
                <Input type="file" id="file5" name="file5" />
                <div className="d-flex justify-content-between">
                  <small className="text-muted">Upload file (max. 10mb)</small>
                </div>
              </FormGroup>
            </Col>
            <Col sm="12" lg="6">
              <FormGroup>
                <Label htmlFor="file6">Metode Pelaksanaan</Label>
                <Input type="file" id="file6" name="file6" />
                <div className="d-flex justify-content-between">
                  <small className="text-muted">Upload file (max. 10mb)</small>
                </div>
              </FormGroup>
            </Col>
          </Row>
          <Row>
            <Col sm="12" lg="6">
              <FormGroup>
                <Label htmlFor="file5">QA/QC Plan</Label>
                <Input type="file" id="file5" name="file5" />
                <div className="d-flex justify-content-between">
                  <small className="text-muted">Upload file (max. 10mb)</small>
                </div>
              </FormGroup>
            </Col>
          </Row> */}
        <hr />
        <FormGroup className="d-flex justify-content-end">
          {isSubmitting ? (
            <Button type="button" color="primary" size="sm">
              <div className="d-flex align-items-center gap-2">
                <Spinner size="sm" />
                Mengirim..
              </div>
            </Button>
          ) : data?.status === 'submit_dokumen' ? (
            <Button
              type="button"
              color="success"
              className="me-2 d-flex gap-2 align-items-center"
              size="sm"
              disabled
            >
              <MaterialIcon icon="check" />
              Submitted
            </Button>
          ) : (
            <Button
              type="button"
              color="primary"
              className="me-2 d-flex gap-2 align-items-center"
              size="sm"
              disabled={!isCompleted}
              onClick={handleSubmitDocument}
            >
              <MaterialIcon icon="send" />
              Submit Dokumen
            </Button>
          )}
        </FormGroup>
      </CardBody>
    </Card>
  );
};

export default UploadDocument;
